import { Component, OnInit } from '@angular/core';
import { AuthService } from 'src/app/services/auth.service';

@Component({
  selector: 'app-default',
  templateUrl: './default.component.html',
  styleUrls: ['./default.component.scss']
})
export class DefaultComponent implements OnInit {

  
  openSidebar = true;
  user:any = {};
  avater:any = '';
  
  constructor(private auth:AuthService) { }

  ngOnInit() {
    this.auth.me().subscribe((res:any) =>{
      this.user.name = res.payload.name,
      this.user.email = res.payload.email
      this.user.thumbnail = res.avater
      // console.log(res.payload.name);
    });

    this.auth.userThumbnail.subscribe((data:any) => {
      this.user.thumbnail = data;
    })

  }
  toggleMenu(evt){
     this.openSidebar = !this.openSidebar;
  }

}
