import { Component, OnInit, ElementRef } from '@angular/core';
import * as $ from 'jquery';
import { AuthService } from 'src/app/services/auth.service';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss']
})
export class ProfileComponent implements OnInit {
  user:any = {};
  constructor(private auth:AuthService, private _el:ElementRef) {

   }

  ngOnInit() {
    $(function(){

      $(".avater-wrap").bind('click',function(){
        $(".avater-file").click();
      })

      $("#thumbnail").on('change', function(){
        const file = $(this).get(0).files;

        let reader = new FileReader();
        reader.readAsDataURL(file[0]);
        reader.addEventListener('load', function(e){
          // let img = e.target.result;
          // $("#avater-img").attr('src', img);
          $(".avater-save-btn").css('display','inline-block');
        })
      })
    })

    
    this.auth.me().subscribe((res:any) =>{   
      this.user.name = res.payload.name,
      this.user.email = res.payload.email
      this.user.thumbnail = res.avater
    });

    this.auth.userThumbnail.subscribe((data:any) => {
      this.user.thumbnail = data;
    })
  }

  avaterUpload(){
    console.log('hi');
    const frmData = new FormData(this._el.nativeElement.querySelector('form'));
    console.log(frmData);
    // this.auth.uploadFile(frmData);
  }

}
