import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DefaultComponent } from './layouts/default/default.component';
import { DashboardComponent } from './modules/dashboard/dashboard.component';
import { LoginComponent } from './modules/login/login.component';
import { SignupComponent } from './modules/signup/signup.component';
import { AuthGuard } from './Guards/auth.guard';
import { GuestGuard } from './Guards/guest.guard';
import { ProfileComponent } from './modules/profile/profile.component';
import { PagenotfoundComponent } from './shared/components/pagenotfound/pagenotfound.component';
import { HomeComponent } from './fornt/components/home/home.component';
import { ContactComponent } from './fornt/components/contact/contact.component';
import { LayoutComponent } from './fornt/mainlayout/layout/layout.component';
import { NavbarComponent } from './fornt/components/navbar/navbar.component';
import { PostComponent } from './modules/post/post.component';
import { MenuComponent } from './fornt/components/menu/menu.component';
import { SpecialComponent } from './fornt/components/special/special.component';
import { BlogComponent } from './fornt/components/blog/blog.component';
import { ReservationComponent } from './fornt/components/reservation/reservation.component';
import { AboutComponent } from './fornt/components/about/about.component';


const routes: Routes = [{
  path:'admin',
  component:DefaultComponent,
  canActivate:[AuthGuard],
  children:[{
    path:'',
    component:DashboardComponent,
    canActivate:[AuthGuard],

  },
  {
    path:'posts',
    component:PostComponent,
    canActivate:[AuthGuard],

  },
  {
    path:'profile',
    component:ProfileComponent,
    canActivate:[AuthGuard]
  }],
},
{

  // path:'', redirectTo:'login', pathMatch:'full',
  path:'login',
  component:LoginComponent,
  canActivate:[GuestGuard]
},
{
  path:'signup',
  component:SignupComponent,
  canActivate:[GuestGuard]

},
{
  path:'',
  // loadChildren:'./fornt/components/home/home.module#HomeModule'
  component:LayoutComponent,
  children:[
    {
      path:'',
      component:HomeComponent
    },
    {
      path:'menu',
      component:MenuComponent
    },
    {
      path:'special',
      component:SpecialComponent
    },
    {
       path:'blog',
       component:BlogComponent
    },
    {
      path:'reservation',
      component:ReservationComponent
   },
   {
    path:'about',
    component:AboutComponent
   },
   {
    path:'contact',
    component:ContactComponent
   }

  ]
},

// {
//   path:'',
//   loadChildren:'./fornt/components/contact/contact.module#ContactModule'
//   // component:ContactComponent
// },
// {
//   path:'',
//   redirectTo:'',
//   pathMatch:'full'
// },
{
  path:'**',
  component:PagenotfoundComponent
}

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
