import { Component, OnInit } from '@angular/core';
import $ from "jquery";

@Component({
  selector: 'app-special',
  templateUrl: './special.component.html',
  styleUrls: ['./special.component.scss']
})
export class SpecialComponent implements OnInit {

  constructor() { }

  ngOnInit() {
    $(function(){
      $(".h-wrap-imgs").mouseenter(function(){
        $(this).find(".p-overlay").animate({
          opacity:"1"
        });
        $(this).find(".desc-overlay").animate({
          opacity:"1"
        });
        
       }).mouseleave(function(){
        $(this).find(".p-overlay").animate({
          opacity:"0"
        });
        $(this).find(".desc-overlay").animate({
          opacity:"0"
        });
      })
    })
  }

}
