import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { NgForm } from '@angular/forms';
import { CategoryService } from 'src/app/services/category.service';
import * as $ from 'jquery';
import { PostService } from 'src/app/services/post.service';
import { MatSnackBar } from '@angular/material';

@Component({
  selector: 'app-post',
  templateUrl: './post.component.html',
  styleUrls: ['./post.component.scss']
})
export class PostComponent implements OnInit {
  // @ViewChild('postForm') postForm:NgForm;
  viewErrors : any = null;
  categories:any;
  show:boolean = false;
  // selectedFile: File = null;

  constructor(private catService:CategoryService, private pService:PostService, 
    private snackbar:MatSnackBar, private _el: ElementRef) {
    this.catService.getAllCategories().subscribe((data) =>{
      this.categories = data;
    })

    $(function(){
      $(".upload-file-btn").bind('click', function(){
        $("#img").click();
      })
      
      $("#img").on('change', function(){
        const file = $(this).get(0).files;

        let reader = new FileReader();
        reader.readAsDataURL(file[0]);
        reader.addEventListener('load', function(e){
          // let img = e.target.result;
          // $("#post-thumbnail").attr('src', img);
        })
      })
    })
   }

  ngOnInit() {
    
  }

  // uploadFile(event){
  //    this.selectedFile = <File>event.target.files[0];
  // }  
  onSubmit(p:any){
    // console.log(this.postForm.value.validformgroup);
    let frmData = new FormData(this._el.nativeElement.querySelector('form'));
    // fd.append('file', this.selectedFile, this.selectedFile.name);
    
    // this.postForm.reset();
    this.show = false;
    this.pService.onSubmitPost(frmData).subscribe((data:any)=>{
      if(data.errors){
         
         this.viewErrors = data.errors;
         this.show = !this.show;
      }else{
        this.snackbar.open(data.msg,"Successed",{
          duration:3000,
        })
      }

      
    })
  }

}
