import { Injectable, EventEmitter } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs/internal/BehaviorSubject';

@Injectable({
  providedIn: 'root'
})

export class AuthService {
  
  userThumbnail = new EventEmitter();

  constructor(private http:HttpClient) { }

  behaviorObj = new BehaviorSubject<boolean>(false);

  isUserLogedIn = this.behaviorObj.asObservable();

  checkedLogin(){
     const token = localStorage.getItem('access_token');
     
     if(token){
       this.behaviorObj.next(true);
     }else{
       this.behaviorObj.next(false);
     }
  }

  //register user

  register(user:any){
    return this.http.post('http://localhost:3000/auth/register', user);
  }

  //login user


  login(user:any){
      this.http.post('http://localhost:3000/auth/login',user).subscribe((res:any) =>{
      if(res.access_token){
        localStorage.setItem('access_token', res.access_token);
        localStorage.setItem('user', JSON.stringify(res.user));

        this.checkedLogin();
      }

    });
  }

  //get user info
  me(){
    return this.http.post('http://localhost:3000/auth/me',{});
  }

  //upload user file
  uploadFile(thumbnail: any){
    this.http.post("http://localhost:3000/auth/upload",thumbnail)
    .subscribe((res:any) =>{
       this.userThumbnail.emit(res);
    });
  }

  // logout user
  signOut(){
    return this.http.post('http://localhost:3000/auth/logout',{}).subscribe((res:any) =>{
      console.log(res);
      localStorage.removeItem('access_token');
      localStorage.removeItem('user');
      this.checkedLogin();
    })
  }
}
