import { Component, OnInit } from '@angular/core';
import gsap from "gsap";

declare const ScrollMagic:any;
@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.scss']
})
export class AboutComponent implements OnInit {

  constructor() { }

  ngOnInit() {
    const tl = gsap.timeline();
    const controller = new ScrollMagic.Controller();

      tl.from(".h-text", {y:100, duration:0.8, opacity:0, ease:"back"})
           .from(".about-desc",{y:-100, duration:0.8, opacity:0, ease:"back"})
           .from(".h-img", {x:150, duration:0.8, opacity:0, ease:"back"});
      // .from(".reservation-text",{y:100, opacity:0, duration:0.8, ease:'back'});
      // const scene = new ScrollMagic.Scene({
      //   triggerElement:".reservation-head-wrap",
      //   triggerHook:"onLeave",
      //   duration:"100%"
      // }).setPin(".reservation-head-wrap").setTween(tl.play()).addTo(controller);
  }

}
