import { Component, OnInit,Input } from '@angular/core';
import * as Highcharts from 'highcharts';
import HC_exporting from 'highcharts/modules/exporting';

@Component({
  selector: 'app-card',
  templateUrl: './card.component.html',
  styleUrls: ['./card.component.scss']
})
export class CardComponent implements OnInit {

  @Input() label:string;
  @Input() percentage:string;
  @Input() total:string;
  @Input() cardData:any;

  chartOptions = {};
  Highcharts = Highcharts;

  constructor() { }

  ngOnInit() {
    this.chartOptions = {
              chart: {
                type: 'area',
                backgroundColor:null,
                borderwidth:0,
                margin:[2,2,2,2],
                height:60
            },
            title: {
                text: null
            },
            subtitle: {
                text: null
            },
   
            tooltip: {
                split: true,
                // valueSuffix: ' millions'
                outside:true
            },
            legend: {
              enabled:false
            },
            credits:{
              enabled:true
            },
            exporting:{
              enabled:false
            },
            xAxis:{
               labels:{
                  enabled:false
               },
               title:{
                 text:null
               },
               startOnTick:false,
               endOnTick:false,
               tickOptions:[]
            },
            yAxis:{
              labels:{
                 enabled:false
              },
              title:{
                text:null
              },
              startOnTick:false,
              endOnTick:false,
              tickOptions:[]
           },
            series:this.cardData
        }
        HC_exporting(Highcharts);
        setTimeout(()=>{
          window.dispatchEvent( new Event('resize'))
        },300)
    }
  

}
