import { Component, OnInit,Input , Inject} from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA, MatSnackBar } from '@angular/material';
import { CategoryService } from 'src/app/services/category.service';
export interface DialogData {
  animal: string;
  name: string;

}
@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.scss']
})
export class SidebarComponent implements OnInit {
  animal: string;
  name: string;
  title:string;
  constructor(public dialog:MatDialog) { }

  openDialog(): void {
    const dialogRef = this.dialog.open(CategoryModalComponent, {
      width: '530px',
      data: {name: this.name, animal: this.animal}
    });

    dialogRef.afterClosed().subscribe(result => {
      this.animal = result;
    });
  }

  ngOnInit() {}

}


@Component({
  selector: 'app-category-modal',
  templateUrl: './../category-modal/category-modal.component.html',
  styleUrls: ['./../category-modal/category-modal.component.scss']

})
export class CategoryModalComponent {

  
  constructor(
    public dialogRef: MatDialogRef<CategoryModalComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData,
     public cat: CategoryService, public scnakBar:MatSnackBar) {}

  onNoClick(): void {
    this.dialogRef.close();
  }

  submitCategory(category:any){
    let cat = {title: category.control.value.title}
     this.cat.addCategory(cat).subscribe((res:any) => {
       if(res.errors){
         this.scnakBar.open(res.errors.title, "Error",{
          duration:3000
         })
       }
       if(res.success){
         this.scnakBar.open(res.success, "Successed",{
           duration:3000
         })
       }


       this.dialogRef.close();

     })
    
  }


}