import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { NgForm } from "@angular/forms";
// import { TimelineMax, TweenMax, }  from "gsap";
import $ from "jquery";

import gsap from "gsap";
import { ReservationService } from 'src/app/services/reservation.service';

declare const ScrollMagic:any;

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
  
  // @ViewChild('reserForm') reserForm : NgForm;

  reservationForm  = "reservation-overlay";
  rHeadWrapCls = "r-head-wrap";
  
  constructor(private _el:ElementRef, private reService:ReservationService) {  }

  ngOnInit() {
    const tl = gsap.timeline();
    gsap.from(".slider-cap",1, {y:100,opacity:0, ease:"back"});
    // gsap.from(".slider-h1",.8, {y:100, opacity:0, ease:"back"});
    // gsap.from(".slider-p",.8, {y:100, opacity:0, ease:"back"});
    const controller = new ScrollMagic.Controller();
    tl.from(".v-heading", .2, {x:300, opacity:0})
    .from(".v-heading-border-bottom", .2, {x:300, opacity:0})
    .from(".v-box", .2, {y:-150, opacity:0})
    .from(".v-img-1", .2, {x:200,opacity:0})
    .from(".v-img-2", .2 ,{x: -150, opacity:0})
    const scene = new ScrollMagic.Scene({
      triggerElement:".video-sec",
      triggerHook:"onLeave",
      duration:"100%"
    })
    .setClassToggle("#v-text", "v-text")
    .setPin(".video-sec")
    .setTween(tl.play())
    .addTo(controller);
    
      tl.progress()
    //   console.log(tl.progress);



     this.showPanel(0);
  }
  
  showPanel(index){
    let tabs = this._el.nativeElement.querySelectorAll(".menu .tabs");
    let menuList = this._el.nativeElement.querySelectorAll(".menu-list a");

    tabs.forEach(function(node){
      node.style.display = "none";
    })
    
    tabs[index].style.display = "flex";
    
     
    menuList.forEach(function(a){
       a.addEventListener('click',function(){
         menuList.forEach(crt => crt.classList.remove('under-line'));
         this.classList.add('under-line');
       })
    })

  }

  onSubmitForm(){
    // console.log(this.reserForm.value.resForm);
    // this.reService.makeReser(this.reserForm.value.resForm).subscribe((res:any) => {
    //   console.log(res);
    // });
  }


}
